import Swal from "sweetalert2";
import React from "react";
import { Modal, Icon } from "antd";
import getLanguage from "./MyLanguange";

// plugin: cordova-plugin-device
const checkPlatform = () => {
  let platform = "";
  if (window.cordova && window.device) {
    platform = window.device.platform;
  }

  return platform;
};

// cordova: ask mobile permission (storage permission)
const downloadPermission = (url, item_name, file_type) => {
  console.log(
    navigator.userAgent,
    !!window.cordova,
    typeof cordova == "object",
    window.hasOwnProperty("cordova")
  );
  console.log(window);
  if (!!window.cordova) {
    let platform = checkPlatform();
    console.log(platform);
    if (platform === "Android") {
      let permissions = window.cordova.plugins.permissions;

      permissions.checkPermission(
        permissions.WRITE_EXTERNAL_STORAGE,
        status => {
          let permissions = window.cordova.plugins.permissions;
          if (!status.hasPermission) {
            // asking permission to the user
            permissions.requestPermission(
              permissions.WRITE_EXTERNAL_STORAGE,
              req_status => {
                if (req_status.hasPermission) {
                  // proceed with downloading
                  downloadFile(url, platform, item_name, file_type);
                }
              },
              null // error callback
            );
          } else {
            downloadFile(url, platform, item_name, file_type);
          }
        },
        null
      );
    } else {
      downloadFile(url, platform, item_name, file_type);
    }
  } else {
    console.log("Broser");

    let a = document.createElement("a");
    a.href = url;
    a.download = item_name;
    a.target = "_blank";
    document.body.appendChild(a);
    a.click();
    // return
  }
};

// cordova: download
const downloadFile = (url, platform, item_name, file_type) => {
  let file_name = item_name;
  let file_path =
    window.cordova.file.externalRootDirectory + "download/" + file_name;
  let confirm_button_text = getLanguage({ id: "openFile" });
  // change directory & button text for iOS
  if (platform === "iOS") {
    file_path = window.cordova.file.documentsDirectory + file_name;
    confirm_button_text = getLanguage({ id: "saveFile" });
  }
  let file_transfer = new window.FileTransfer();
  let uri = encodeURI(decodeURIComponent(url));
  let mime_type;

  if (file_type === "pdf") {
    mime_type = "application/pdf";
  } else {
    mime_type = "image/" + file_type;
  }

  // Downloading the file
  file_transfer.download(
    uri,
    file_path,
    entry => {
      Modal.confirm({
        icon: <Icon type="check-circle" />,
        type: "success",
        title: getLanguage({ id: "successfullyDownloaded" }),
        centered: true,
        okText: confirm_button_text,
        cancelText: getLanguage({ id: "close" }),
        okButtonProps: { size: "large" },
        cancelButtonProps: { size: "large" },
        onOk() {
          console.log("OK");
          window.cordova.plugins.fileOpener2.showOpenWithDialog(
            entry.nativeURL, // can also use a Cordova-style file uri: cdvfile://localhost/persistent/Downloads/starwars.pdf
            mime_type,
            {
              error: function error(err) {
                console.error(err);
                Modal.error({
                  title: getLanguage({ id: "failedToDownload" }),
                  centered: true,
                  okText: getLanguage({ id: "close" }),
                  okButtonProps: { size: "large" },
                  onOk() {
                    console.log("OK");
                  }
                });
              },
              success: function success() {
                console.log("success with opening the file");
              }
            }
          );
        },
        onCancel() {
          console.log("Cancel");
        }
      });
    },
    error => {
      Modal.error({
        title: getLanguage({ id: "failedToDownload" }),
        centered: true,
        okText: getLanguage({ id: "close" }),
        okButtonProps: { size: "large" },
        onOk() {
          console.log("OK");
        }
      });
    },
    false
  );
};

export { downloadPermission, downloadFile, checkPlatform };
